import React from 'react';

class ConnectionError extends React.Component {

    render() {
        const markup =
            <li><span role="img" aria-label="flash-emoji"></span> Verbindung zu <a href="https://www.meetup.com/JUG-Mainz/">Meetup</a> konnte nicht aufgebaut werden <span role="img" aria-label="flash-emoji"></span></li>

        return markup
    }
}

export default ConnectionError



