import React from 'react'
import fetchJsonp from 'fetch-jsonp'
import Event from './Event.jsx'
import ConnectionError from "./ConnectionError";

        class UpcomingEvents extends React.Component {
            constructor(props) {
                super(props)
                this.state = {events: [],
                              exception: false,
                              collapsed: true}
            }

            componentDidMount() {
                fetchJsonp(this.props.uri,
                        {jsonpCallbackFunction: this.props.callback})
                        .then(response => response.json())
                        .then((data) => this.setState({events: data.data, exception: false}))
                        .catch(this.setState({exception: true}))
                
                this.toggle = this.toggle.bind(this);
            }
            
            toggle() {
                let collapsed = this.state.collapsed
                this.setState({collapsed: !collapsed})
            }

            render() {
                let exceptionExists = this.state.exception
                let entriesExist = this.state.events.length > 0
                const eventList = entriesExist ?
                        <div>
                            <ul className="event-list">
                                <Event key={this.state.events[0].id} event={this.state.events[0]}/>
                                {
                                this.state.events.filter(() => !this.state.collapsed)
                                                 .slice(1, 5)
                                                 .map((event) => <Event key={event.id} event={event}/>)}
                            </ul>
                            {(this.state.events != null 
                                    && this.state.events.length > 1) 
                                    ? <a href="#/" className="see-more-events" onClick={this.toggle}>{this.state.collapsed ? (this.state.events.length - 1) + ' mehr sehen' : 'Weniger sehen'}</a> 
                                    : ''}
                        </div>
                        :
                        (exceptionExists === true ?
                        <div>
                            <ul className="event-list">
                                <ConnectionError/>
                            </ul>
                        </div>
                        :
                        <div>
                            <ul className="event-list">
                                <li>Keine ausstehenden Events - wir sind dabei, das zu ndern <span role="img" aria-label="Happy Smiley"></span></li>
                            </ul>
                        </div>)

                return eventList
            }
        }

        export default UpcomingEvents


