import React from 'react'
import {render} from 'react-dom'
import UpcomingEvents from './UpcomingEvents.jsx'
import Events from './Events.jsx'
import TwitterTimeline from './TwitterTimeline.jsx'
import SponsorList from './SponsorList.jsx'
import './main.scss'


render(<UpcomingEvents callback="upcoming"
                       uri="https://7h3fmpyznc.execute-api.us-east-1.amazonaws.com/Production/meetup/upcoming"/>, document.getElementById('upcoming'))
render(<Events callback="past"
               uri="https://7h3fmpyznc.execute-api.us-east-1.amazonaws.com/Production/meetup/past"/>, document.getElementById('past'))
render(<SponsorList/>, document.getElementById('sponsors'))
render(<TwitterTimeline/>, document.getElementById('twitter'))


